
.review {
  &-info {
    padding: 20px 18px;
    p {
      margin-bottom: 8px;
      font-family: 'appleB';
      font-size: size(15);
      font-weight: 500;
      color: $black-two;
      .text-primary {
        font-weight: 600;
      }
    }
  }
  &-point {
    position: relative;
    font-family: 'appleL';
    font-size: size(12);
    line-height: 1.3;
    a {
      color: $french-blue;
    }
    .btn-dark {
      position: absolute;
      right: 20px;
      bottom: 4px;
      padding: 5px 14px;
    }
  }
  // 리뷰 게시물
  &-article {
    position: relative;
    padding: size(20) 0;
    border-bottom: 1px solid $pinkish-grey-five;
    .btn-wrap {
      position: absolute;
      top: 15px;
      right: -10px;
    }
  }
  &-header {
    display: flex;
    align-items: baseline;
    margin-bottom: size(12);
    .star-rate {
      display: contents;
      .icon-rate {
        margin-right: 2px;
      }
    }
    .rate-num {
      font-size: size(14);
      white-space: nowrap;
      b {
        display: inline-flex;
        align-items: baseline;
        font-family: 'appleEB';
      }
    }
    .divider-h {
      margin: 0 10px;
    }
    .name {
      width: 50px;
      font-size: size(13);
      font-family: 'appleL';
      &.sns {
        font-family: 'appleH';
      }
    }
    .date {
      margin-left: 6px;
      font-size: size(12);
      color: $pinkish-grey;
    }
  }
  &-body {
    p {
      margin-bottom: 0;
      font-size: size(13);
    }
    .thumb-review {
      display: flex;
      align-items: flex-start;
      margin-top: 12px;
      img {
        width: calc(50% - 12px);
        max-width: size(160);
        height: auto;
        margin-right: 12px;
        &:nth-child(2) {
          margin-right: 0;
        }
      }
    }
  }
}
