.tag {
  display: inline-block;
  // height: 30px;
  height: size(30);
  // padding: 5px 14px;
  padding: size(5) size(14);
  margin: 2px 2px 6px;
  // border-radius: 15px;
  border-radius: size(15);
  border: 1px solid $greyish-three;
  // font-size: 13px;
  font-size: size(13);
  background-color: $white;
  @include on-event {
    border-color: $primary;
    background-color: $primary;
    color: $white;
  }
  &-wrapper {
    margin-left: -2px;
  }
}