.detail {
  // margin-top: size(20);
  &-heading {
    h1 {
      font-family: 'appleB';
      font-size: size(21);
      letter-spacing: -1px;
    }
    .address {
      font-family: 'appleL';
      font-size: size(13);
      color: $warm-grey-two;
    }
    .btn-light {
      margin-left: 6px;
    }
  }
  .star-rate {
    > * {
      vertical-align: middle;
    }
  }
}

// 쿠폰 style
.coupon-wrap {
  &.type1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: size(56);
    font-size: size(15);
    font-family: 'appleB';
    font-weight: 300;
    color: $white;
    background-image: linear-gradient(to right, #529faf 33%, #314195 88%);
    .icon-gift {
      width: 20px;
      margin-right: 4px;
    }
    .text-sky {
      color: $pale-sky-blue;
    }
  }
}

// 패키지 정보
.package-info {
  padding: size(22) size(19);
  background-color: $white-four;
  b {
    font-family: 'appleL';
    font-size: size(15);
  }
  ul {
    list-style: none;
    margin-top: size(13);
    margin-left: 0;
    padding-left: 1em;
    text-indent: -1em;
    li {
      margin-bottom: 2px;
      word-break: keep-all;
      &:before {
        content: '\22C5';
        margin-right: 4px;
        font-size: size(13);
      }
    }
  }
}

// 예약일자
.book-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: size(57);
  font-family: 'appleL';
  font-size: size(14);
  color: $black-two;
  background-color: $white-five;
  a {
    font-family: 'appleSB';
    font-weight: 600;
    color: $warm-blue;
  }
}

// 객실더보기
.more-rooms {
  height: 0;
  overflow: hidden;
  transition: height .3s ease;
  &.show {
    height: auto;
  }
}

// 공지 및 온셥정보
.notice-info {
  padding: size(40) 0;
  h5 {
    margin: size(30) 0 size(18);
    font-family: 'appleB';
    font-size: size(16);
    color: $black-two;
    &:first-child {
      margin-top: 0;
    }
  }
  .list {
    line-height: 1.5;
    font-size: size(14);
  }
  .remark {
    margin: 0;
    font-size: size(12);
    font-family: 'appleL';
    color: $greyish-brown;
  }
}

// 지도 
.map-api-container {
  position: relative;
  height: size(190);
  background-color: #ccc; // 지도 영역 임시 배경색
  .btn-wrap {
    position: absolute;
    bottom: size(13);
  }
}
.map-address {
  padding: size(19) auto;
  font-size: size(12);
  color: $black-two;
}
