.badge {
  &-new {
    height: 15px;
    padding: 3px 4px;
    border-radius: 7px;
    font-family: 'appleM';
    font-size: size(8);
    background-color: $pastel-red;
  }
  &-md {
    width: auto !important;
    height: 20px;
    padding: 5px 12px;
    border-radius: size(15);
    font-size: size(9);
    font-family: 'appleL';
    font-weight: 600;
    line-height: 1;
    color: $warm-blue;
    background-color: $very-light-blue;
  }
  &-circle {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: size(54);
    height: size(54);
    border-radius: 50%;
    font-family: 'appleH';
    font-size: size(18);
    background-color: $white-five;
    &.enjoy {
      color: $primary;
    }
    &.kakao {
      color: $vomit;
    }
    &.naver {
      color: $algae-green;
    }
    &.fb {
      color: $azul;
    }
  }
}

.badge-custom {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: size(20);
  padding: 4px 8px;
  border-radius: 0;
  font-family: 'appleL';
  font-weight: 300;
  font-size: size(11);
  line-height: 2;
  letter-spacing: 0;
  &-grade {
    width: size(50);
    color: $white;
    background-color: $charcoal-grey;
  }
  &-type {
    font-weight: 700;
    color: $french-blue;
    background-color: $white-three;
  }
  &-pension {
    font-weight: 500;
    color: $white;
    background-color: $periwinkle;
  }
  &-hanok {
    font-weight: 500;
    color: $white;
    background-color: $greyish-brown;
  }
  &-motel {
    font-weight: 700;
    color: $french-blue;
    border: solid 1px $french-blue;
    background-color: $white;
  }
  &-timesale {
    padding: 4px 8px;
    font-weight: 500;
    color: $white;
    border: solid 1px $periwinkle;
    background-color: $warm-blue;
    .icon {
      width: 10px;
      height: 10px;
      margin-right: 6px;
    }
  }
  &-dayuse {
    font-weight: 500;
    color: $warm-blue;
  }
}

