// added New Star rate
.rate-group {
  width: 100%;
  display: inline-flex;
  justify-content: center;
  label {
    width: size(26);
    height: size(26);
    font-size: 2rem;
    margin: 0 .35rem;
    .fa-star {
      color: $pastel-red;
    }
  }
  input {
    width: 0;
    height: 0;
    visibility: hidden;
    &:checked ~ label .fa-star {
      color: $pinkish-grey-two;
    }
    &:hover ~ label .fa-star {
      color: $pinkish-grey-two;
    }
  }
}