// calendar pop ui

.week-header {
  position: fixed;
  z-index: 6;
  top: 50px;
  width: 100%;
  .heading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: size(30);
    margin: 0;
    font-size: size(12);
    color: $greyish-brown;
    background-color: $white-four;
  }
}

.calendar {
  // width: 100%;
  // padding: 1rem;
  // border-radius: .5rem;
  // box-shadow: 1px 1px 3px 2px #ccc;
  &-header {
    display: flex;
    justify-content: center;
    align-items: center;
    h2 {
      font-size: size(18);
      margin-bottom: 0;
    }
  }
}

.calendar-week {
  display: flex;
  justify-content: space-between;
  li {
    width: calc(100% / 7);
    height: 36px;
    margin: 0;
    padding: .571rem;
    font-size: size(11);
    text-align: center;
    background-color: $white;
  }
}

.calendar-days {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  flex: 0 0 auto;
  li {
    position: relative;
    width: calc(100% / 7);
    margin: 0;
    padding: 0;
    text-align: center;
    &::after {
      content: '';
      display: block;
      padding-top: 100%; /* 1:1 Aspect Ratio */
    }
    span {
      position: absolute;
      z-index: 3;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      font-size: size(15);
      color: $black-two;
      transition: all .2s ease;
    }
    @media screen and (max-width: 600px) {
      span {
        // font-size: .875rem;
        font-size: 1rem;
      }
    }
    &.today span {
      background-color: $white-six;
    }
    &.out span {
      color: $pinkish-grey-eight;
    }
    &:not(.range) span:hover, 
    &:not(.range) span:active, 
    &:not(.range) span:focus,
    &.start span {
      padding-top: 1rem;
      color: $white;
      background-color: $french-blue;
      &::after {
        content: '체크인';
        position: absolute;
        top: 16%;
        font-size: size(9);
      }
    }
    // &:not(.range):not(.start) span:hover, 
    // &:not(.range):not(.start) span:active, 
    // &:not(.range):not(.start) span:focus,
    &.end span {
      padding-top: 1rem;
      color: #fff;
      background-color: $charcoal-grey-two;
      &::after {
        content: '체크아웃';
        position: absolute;
        top: 16%;
        font-size: size(9);
      }
    }
    &.range::before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 0;
      background-color: #f5f5f5;
    }
    &.range.start::before {
      left: auto;
      right: 0;
      width: 50%;
    }
    &.range.end::before {
      right: 0;
      width: 50%;
    }
  }
}

