// svg 아이콘 컬러 변경 방법
//
// 정의된 컬러 class 사용 (필요 컬러는 추가)
// &-white { background-color: $primary; }
// <span class="icon icon-name icon-white">

.icon {
  display: inline-block;
  width: size(24);
  height: size(24);
  border: 0;
  vertical-align: middle; 
  outline: none;
  background: none;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-color: $warm-grey;
  mask-position: center center !important;
  mask-repeat: no-repeat;
  mask-size: contain;
  transition: all .3s ease;
  &-nav {
    width: 27px;
    height: 24px;
  }
  &-heading {
    width: 22px;
    height: 25px;
    &.live {
      width: 36px;
      height: 19px;
    }
  }
  &-rate {
    width: 13px;
    height: 12px;
    background-color: $pinkish-grey-two;
    &.on {
      background-color: $pastel-red;
    }
  }
  &-xs {
    width: 6px;
  }
  &-sm {
    width: 12px;
    height: 12px;
  }
  &-md {
    width: 20px;
    height: 20px;
  }
  &-ml {
    width: size(15);
    height: size(15);
  }
  // colors
  &-white {
    background-color: $white;
  }
  &-gb {
    background-color: $greyish-brown;
  }
  &-fb {
    background-color: $french-blue;
  }
  &-red {
    background-color: $pale-red;
  }
  &-wg {
    background-color: $warm-grey;
  }

  &-pgn{
    background-color:$pinkish-grey-nine;
  }

  &-cgt{
    background-color:$charcoal-grey-two;
  }

  &-gb{
    background-color:$greyish-brown;
  }

