// -----------------------------------------------------------------------------
// Basic styles
// -----------------------------------------------------------------------------

// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/base/_base.scss

html,
body {
  height: 100%;
  letter-spacing: -.047rem;
  font-family: 'appleL';

  @include media-breakpoint-up(md) {
    @include font-size(18px);
  }

}

ul, ol, dl {
  list-style: none;
  margin: 0;
  padding: 0;
}

//hr
hr{
  &.grey{
    margin-top: 1.125rem;
    margin-bottom:1.125rem;
    border:1px solid $pinkish-grey-five;
  }
}

//font color
.text{
  &-french-blue{
    color:$french-blue !important;
  }

  &-pastel-red{
    color:$pastel-red !important;
  }
  &-pinkish-grey-nine{
    color:$pinkish-grey-nine !important;
    &.sm{
      font-size: size(13);
    }
  }

  
}




a {
  // color: $black-two;
  @include on-event(true) {
    color: $black-two;
    text-decoration: none;
    outline: none;
  }
  // 특정 요소 link 적용
  > object, svg {
    pointer-events: none;
  }
}


// Placeholder color
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $warm-grey-three;
  opacity: 1; /* Firefox */
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: $warm-grey-three;
}
::-ms-input-placeholder { /* Microsoft Edge */
  color: $warm-grey-three;
}

// main padding
.ui {
  &-nav {
    // nav-height = 66
    padding-bottom: 66px;
  }
  &-header {
    padding-top: 55px;
  }
  &-header-tab {
    padding-top: size(90);
  }
  &-nav-search {
    padding-top: 160px;
  }
  &-calendar {
    padding-top: 120px;
  }
}


// thumbnail common
.thumb {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

img {
  width: 100%;
}

// 배경있는 텍스트
.text-bg {
  display: inline-block;
  padding: 4px 8px;
  font-family: 'appleL';
  font-size: size(11);
  font-weight: 400;
  color: $white;
  &-round {
    border-radius: 20px;
    background-color: rgba(42, 42, 42, 0.7);
  }
}

// ul, ol 목록
.list {
  list-style: none;
  // margin-left: 8px;
  line-height: 1.2;
  li {
    padding-left: 8px;
    word-break: keep-all;
  }
  &-normal {
    li::before {
      content: '\22C5';
      margin-left: -8px;
      margin-right: 4px;
      font-size: 0.92857rem;
    }
  }
}

// dl 데이타 
.dlist {
  display: flex;
}

// 공통 divider
.divider {
  align-self: center;
  width: 1px;
  height: 100%;
  min-height: 8px;
  margin: 0 6px;
  border-left: 1px solid $pinkish-grey;
  &-h {
    width: 100%;
    border: 0;
    border-top: 4px solid $pale-grey;
    border-bottom: 4px solid $pale-grey;
  }
}

// 공통 화살표
.arrow {
  display: inline-block;
  width: 8px;
  height: 8px;
  vertical-align: middle;
  border: 1px solid $french-blue;
  &-down {
    margin: -6px 4px 0 4px;
    border-top: transparent;
    border-right: transparent;
    transform: rotate(-45deg);
  }
  &-up {
    margin: 3px 4px 0 4px;
    border-bottom: transparent;
    border-left: transparent;
    transform: rotate(-45deg);
  }
  &-right {
    margin: 3px 4px 0 4px;
    border-bottom: transparent;
    border-left: transparent;
    transform: rotate(45deg);
  }
  &-bold {
    border-width: 2px;
  }
  &-black {
    border-color: $black-two;
  }
  &-greytwo {
    border-color: $greyish-brown-two;
  }
}

.bg {
  &-grey {
    position: relative;
    background-color: $white-six;
    &.half::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: size(86);
      background-color: $white;
    }
  }
}

// 공통 가격 
.price {
  &.bold {
    font-family: 'appleEB';
    font-size: size(16);
    small {
      font-size: size(13);
    }
  }
}

//title header
.title-header{
  width: 100%;
  padding:10px 20px 13px 20px;

  &.between{
    display: flex;
    justify-content: space-between;
    align-items: center; 
  }
  &-light{
    background-color:$ice-blue-five;
  }
  
}

//ui modal
.modal-backdrop{
  background-color: $black-three;
  opacity: 0.8 !important;
}