// 7-1 Sass Architecture
// https://sass-guidelin.es/#the-7-1-pattern

// Essential Vendors
// - external libraries that should be loaded before
// @import '~sass-material-colors';
@import '~normalize-scss';

$grid-columns: 16 !default;
$grid-gutter-width: 40px !default;

// Abstracts
// - tools and helpers
@import "abstracts/variables";
@import "abstracts/functions";
@import "abstracts/mixins";
@import "abstracts/animations";

// Vendors
// - CSS from external libraries and frameworks
// @import "vendors/fontawesome";
@import "vendors/fontawesome";
// @import "vendors/googlefonts";
@import "vendors/bootstrap";

// Base
// - standard styles for commonly elements
@import "base/base";
@import "base/helpers";
@import "base/fonts";
@import "base/typography";

// Components
// - for small, reusable pieces
@import "components/buttons";
@import "components/icons";
@import "components/badges";
@import "components/cards";
@import "components/vr360";
@import "components/carousels";
@import "components/videojs";
@import "components/form";
@import "components/starrate";
@import "components/modals";
@import "components/tabs";
@import "components/search";
@import "components/banners";
@import "components/tags";
@import "components/calendar";


// Layout
// - everything that takes part in laying out the site/application
@import "layout/header";
@import "layout/footer";

// Pages
// - page-specific styles
@import "pages/home";
@import "pages/detail";
@import "pages/review";
@import "pages/mypage";
@import "pages/payment";

// Themes
// - for different aspects
@import "themes/default";
