// -----------------------------------------------------------------------------
// Home page
// -----------------------------------------------------------------------------

.main-carousel {
  position: relative;
  article {
    position: relative;
    height: 0;
    min-height: 25vh;
    padding-top: 56.25%;
    resize: horizontal;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    .info-wrap {
      position: absolute;
      top: 16%;
      left: 26px;
      p {
        margin-bottom: 9px;
        font-size: size(12);
        font-family: 'appleL';
        color: $pale-salmon;
        opacity: .9;
        // @include media-breakpoint-up(md) {
        //   font-size: size(14);
        // }
      }
      h1 {
        font-size: size(22);
        font-family: 'appleM';
        font-weight: 500;
        line-height: 1.3;
        color: $ice-blue;
        // @include media-breakpoint-up(md) {
        //   font-size: size(24);
        // }
      }
      .btn {
        margin-top: size(10);
      }
    }
  }
  .slider-counter {
    position: absolute;
    z-index: 9;
    bottom: 15px;
    right: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 39px;
    height: 22px;
    border-radius: 11px;
    font-family: 'appleL';
    font-size: size(9);
    font-weight: 400;
    line-height: 1;
    letter-spacing: 0;
    color: $white;
    background-color: rgba(46, 46, 46, 0.8);
  }
}
.category-link {
  margin-top: size(30);
  margin-bottom: size(29);
  a.col-4 {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 84px;
    text-align: center;
    white-space: nowrap;
    .badge-new {
      position: absolute;
      top: 0;
      right: calc(50% - 36px);
    }
    @include media-breakpoint-up(md) {
      min-height: 100px;
      .svg-icon {
        width: 4vw;
        height: 4vw;
      }
    }
  }
  object {
    width: 32px;
    height: 34px;
    + span {
      display: block;
      margin-top: 7px;
      font-family: 'appleM';
      font-size: size(11);
    }
  }
}


// 오늘만 이가격 carousel card
.owl-carousel {
  &.product {
    picture {
      position: relative;
      // width: 60vw;
      // height: 56.25%;
      .bar-time {
        position: absolute;
        top: 10px;
        left: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 20px;
        padding: 10px;
        border-radius: 10px;
        font-size: size(10);
        letter-spacing: 0;
        line-height: 1;
        color: $white;
        background-color: rgba(42, 42, 42, .7);
      }
    }
    .card-body {
      padding-top: 10px;
      .sup {
        display: inline-block;
        margin-bottom: 3px;
        font-size: size(10);
        color: $warm-grey-three;
      }
      .card-title {
        // width: 60vw;
        span {
          display: inline-block;
          line-height: 1;
          padding-right: 8px;
          margin-right: 7px;
          border-right: 1px solid $pinkish-grey;
        }
      }
      .extra-title {
        margin-bottom: 0;
        font-size: size(16);
        font-family: 'appleM';
        font-weight: 400;
      }
    }
  }
  &.rank {
    .owl-stage-outer {
      // left: -15px;
      // width: calc(100% + 32px);
      // height: 250px;
      .owl-stage {
        padding: 0 15px;
      }
    }
    picture {
      // position: relative;
      // height: 277px / 299px * 100%;
      img {
        // min-height: 150px;
      }
      .ranking {
        position: absolute;
        left: 8px;
        bottom: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: size(36);
        height: size(36);
        border-radius: 50%;
        font-family: 'appleL';
        font-size: size(13);
        color: $white;
        background-color: $pastel-red;
      }      
    }
    .card-body {
      line-height: 1.8;
      .sup {
        font-size: size(12);
        color: $greyish;
      }
    }
  }
}


// Lists Common
.list-circle {
  margin: 0 -9px;
  li {
    a {
      text-align: center;
    }
    .circle-img {
      position: relative;
      border-radius: 50%;
      width: 100%;
      height: auto;
      padding-top: 100%;
      margin-bottom: 15px;
      background-position: center;
      + p {
        font-family: 'appleM';
        color: $black-two;
      }
    }
  }
}
.bg-img {
  position: relative;
  display: block;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.list-odd {
  display: flex;
  li {
    padding-left: 0;
    padding-right: 0;
    .bg-img {
      height: 133px;
      @include media-breakpoint-up(md) {
        height: 240px;
      }
      span {
        position: absolute;
        display: inline-block;
        bottom: 10px;
        left: 0;
        overflow: hidden;
        max-width: 75%;
        height: 28px;
        padding: 4px 10px;
        font-size: size(12);
        color: $white;
        background-color: rgba(45, 45, 45, .8);
      }
    }
  }
}
.list-rect {
  display: flex;
  li {
    width: calc(100% / 3);
    padding-left: 0;
    padding-right: 0;
    .bg-img {
      // height: 88px;
      padding-top: 74.66%;
    }
  }
}
.scroll-tab {
  position: relative;
  border-top: 1px solid $pinkish-grey-two;
   &::after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    border-bottom: 1px solid $pinkish-grey-two;
  }
  .btn {
    position: absolute;
    z-index: 9;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    opacity: 0;
    width: 0;
    padding: 0;
    background-color: $white;
    transition: all .2s;
    .icon-arrow {
      width: 6px;
      height: 20px;
      mask-size: 6px;
    }
    &:first-child {
      left: 0;
      transform: translateY(-50%) rotate(180deg);
    }
    &.active {
      opacity: 1;
      width: 12px;
      padding: 4px;
    }
  }
}
.list-scroll-h {
  overflow-x: scroll;
  // display: flex;
  // flex-wrap: nowrap;
  // justify-content: stretch;
  height: 50px;
  white-space: nowrap;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
  li {
    position: relative;
    display: inline-block;
    transition: all .3s;
    &::after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      z-index: 1;
      width: 0;
      border-bottom: 2px solid;
      opacity: 0;
      transition: all .2s;
    }
    &.active::after {
      left: 0;
      width: 100%;
      border-bottom: 2px solid $primary;
      opacity: 1;
    }
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: size(62);
      height: 50px;
      font-size: size(14);
      font-family: 'appleM';
      color: $black-two;
    }
  }
  
}



// Tabs
.tab {
  position: relative;
  // height: 41px;
  // overflow-x: scroll;
  // overflow-y: visible;
  // border-bottom: 1px solid $pale-grey;
  .nav {
    flex-wrap: nowrap;
    &-tabs {
      border: 0;
      li a {
        white-space: nowrap;
        min-width: 96px;
      }
    }
  }
  .btn-custom-morehash {
    position: absolute;
    z-index: 3;
    top: 6px;
    right: 20px;
  }
}
.tab-list {
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  &:after {
    content: '';
    position: absolute;
    z-index: 9;
    left: auto;
    width: 0;
    bottom: 0;
    border-bottom: 3px solid $black-two;
    transition: all .2s ease;
  }
  li {
    position: relative;
    display: inline-block;
    width: 25vw;
    // margin-left: 20px;
    a {
      display: inline-block;
      width: 100%;
      height: 40px;
      font-size: size(17);
      font-family: 'appleM';
      font-weight: 500;
      line-height: 36px;
      text-align: center;
      color: $warm-grey-three;

      &.active,
      &:active,
      &:focus {
        color: $black-two;
      }
    }
  }
}
