::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
	font-size: size(12);
	font-family: 'appleL';
	font-weight: 300;
  color: $warm-grey-nine;
  opacity: 1; /* Firefox */
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
	font-family: 'appleL';
	font-weight: 300;
  color: $warm-grey-nine;
}
::-ms-input-placeholder { /* Microsoft Edge */
	font-family: 'appleL';
	font-weight: 300;
  color: $warm-grey-nine;
}

//label
label{
  font-family: 'appleM';
}
.underline-input-label{
  margin-bottom:0;
}

// Common Form Elements
.form-control {
	padding: 1.1rem;
	font-size: size(13);
	border-radius: 0;
	border-color: $pinkish-grey-five;
  background-color: $white;
  &-xl {
    height: size(46);
  }
	&-lg {
		height: size(41);
		&::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
			font-size: size(13);
		}
	}
  &-md {
    height: size(37);
  }
  &-sm {
    height: size(32);
  }
  // custom colors
  &.bg {
    &-ws {
      background-color: $white-six;
      border-color: $white-six;
    }
    &-wf {
      background-color: $white-four;
      border-color: $white-four;
    }
  }
  // custom styles
  &.underline {
    padding-left: 0;
    border: 0;
    border-bottom: 1px solid $pinkish-grey-five;
    &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
      line-height: normal;
      color: $pinkish-grey-eight;
		}
  }

  &.noborder {
    border: 0;
    background-color: $white-six;
  }
  &.round {
    border-radius: 30px;
  }
	// Custom input
	&-sp {
		border: 0;
		height: 32px;
		padding: 1rem;
		font-size: size(12);
		color: $greyish-brown;
		background-color: $white-four;
		&:focus {
			background-color: $white-four;
		}
		&:disabled,
		&:read-only {
			background-color: $white-four;
		}
	}
}

// input group custom
.input-group-text {
  background-color: $white;
  border-radius: 0;
}

// Special input + arrow 
.input-arrow {
	position: relative;
	.form-control {
		background-color: $white;
	}
	.arrow {
		position: absolute;
		top: 50%;
		right: 15px;
	}
}

// Custom radio type - button
.button-radio {
	display: flex;
	// margin-bottom: size(10);
	label {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: size(46);
		margin-bottom: .714rem;
		text-align: center;
		border: 1px solid $pinkish-grey-five;
		font-size: size(14);
		font-family: 'appleM';
		color: $brownish-grey-three;
	}
	input[type="radio"] {
		width: 0;
		height: 0;
		visibility: hidden;
		&:checked + label {
			color: $primary;
		}
	}
}


// Custom checkbox
.custom-checkbox {
  // line-height: 24px;
	input {
		width: 0;
		height: 0;
		visibility: hidden;
	}
  .custom-control-label {
    cursor: pointer;
    vertical-align: middle;
    &:before {
      width: 20px;
      height: 20px;
      border-width: 1px;
      border-radius: 2px;
      border-color: $white-seven;
      background-color: $white-seven;      
    }
    &:after {
      transition: background-size 0.15s ease-in-out;
      background-repeat: no-repeat;
      background-position-y: center;
      background-position-x: 50%;
      background-size: 0%;      
    }
  }
  .custom-control-input:checked {
    &~.custom-control-label {
      &::before {
        border-color: $warm-grey;
        background-color: $warm-grey;
      }
      &::after {
        width: 15px !important;
        left: -24px;
        transition: background-size 0.15s ease-in-out;
        background-size: 15px !important;
      }
    }
  }
}

// Custom radio
.custom-radio {
  // line-height: 26px;
	input {
		width: 0;
		height: 0;
		visibility: hidden;
	}
  .custom-control-label {
    cursor: pointer;
    vertical-align: middle;
    &:before {
      width: 22px;
      height: 22px;
      border-width: 2px;
      border-color: $white-seven;
      background-color: $white-seven;
      top: 0;
    }
    &::after {
      width: 22px !important;
      height: 22px !important;
      transition: background-size 0.15s ease-in-out;
      background-position-y: center;
      background-position-x: 50%;
      background-size: 0%;
      top: 0;
    }
  }
  .custom-control-input:checked {
    &~.custom-control-label {
      &::before {
        border-color: $primary;
        background-color: $custom-radio-checked-background-color;
      }
      &::after {
        transition: background-size 0.15s ease-in-out;
        background-size: 16px !important;
      }
    }
  }
}


.switch-wrap {
	position: relative;
	display: inline-flex;
	align-items: center;
	width: 63px;
}
// Custom Switch
.custom-switch {
  padding: 0;
  .custom-control-label::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 63px;
    height: 31px;
    pointer-events: none;
    content: "";
    background-color: $white-ten;
    // border: $pinkish-grey-five solid 1px;
		border: 0;
    transition: all .2s ease;
  }
  .custom-control-label::before {
    left: 0;
    width: 62px;
    pointer-events: all;
    border-radius: 16px;
  }

  .custom-control-label::after {
    content: "";
    position: absolute;
    top: 4px;
    left: 4px;
    display: block;
    width: 23px;
    height: 23px;
    background: no-repeat 50% / 50% 50%;
    // border: 1px solid $pinkish-grey-five;
    // box-shadow: 1px 3px 2px 0 rgba(0,0,0,0.1);
  }
  .custom-control-label::after {
    // left: calc(-3.15rem + 2px);
    // width: calc(2.071rem - 3px);
    // height: calc(2.071rem - 3px);
    // width: 28px;
    // height: 28px;
    background-color: $white;
    border-radius: 14px;
    transition: transform 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }

  .custom-control-input:checked ~ .custom-control-label::after {
    border-color: $white;
    background-color: $white;
    // transform: translateX(2rem);
    transform: translateX(140%);
  }
}

