// 두줄 제한 ... 보이기
.strcut {
  overflow: hidden; 
  text-overflow: ellipsis; 
  display: -webkit-box;
  -webkit-box-orient: vertical; 
  word-wrap: break-word;
  -webkit-line-clamp: 1; /* 라인수 */	
  &-twoline {
    -webkit-line-clamp: 2; /* 라인수 */	
  }
}


// png 컬러 변경 -- 
.filter {
  // display: flex;
  // justify-content: center;
  // align-items: center;
  // width: size(50);
  // height: size(50);
  display:block;
  width :100%;
  padding-top: 100%;
  position: relative;
  border-radius: 100%;
  background-color: $white-nine;
  // @include on-event() {
  //   background-color: $charcoal-grey-two;
  //   img {
  //     filter: brightness(3) contrast(3) saturate(0) ;
  //   }
  // }
  img {
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%, -50%);
    // png 아이콘 크키 비율에 맞게 조정
    &.w-20{
      width:20%;
    }

    &.w-30{
      width: 30%;
    }

    &.w-50{
      width:50%;
    }

    &.w-60{
      width:60%;
    }
    width: 40%;
  }
}




