// -----------------------------------------------------------------------------
// Mixins
// -----------------------------------------------------------------------------

// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/abstracts/_mixins.scss

// 이벤트 
@mixin on-event($self: false) {
  @if $self {
    &,
    &:hover,
    &:active,
    &:focus {
      @content;
    }
  } @else {
    &:hover,
    &:active,
    &:focus {
      @content;
    }
  }
}

// flex 
@mixin flex($align: center) {
  display: flex;
  justify-content: $align;
  align-items: $align;
}

// absolute centering
@mixin absolute-position($zindex: 3) {
  content: '';
  position: absolute;
  z-index: $zindex;
}

// arrow border tool
@mixin arrow($width: 6px, $border: 2px, $direction: left) {
  width: $width;
  height: $width;
  border: $border solid $warm-grey-nine;
  @if $direction == down {
    border-top: 0;
    border-left: 0;
    top: 37%;
    transform: rotate(45deg);
  }
  @if $direction == up {
    border-bottom: 0;
    border-left: 0;
    top: 45%;
    transform: rotate(-45deg);
  }
}