.modal {
  // Custom - top full width
  &-content {
    border: 0;
    border-radius: 0;
  }
  &-header {
    padding: size(12) size(20);
    border: 0;
  }
  &-footer {
    padding: 0 size(20) size(20);
    border: 0;
  }
  &-full {
    margin: 0;
    .modal-header {
      position: relative;
      .modal-title {
        width: 100%;
        padding-bottom: size(10);
        border-bottom: 2px solid $white-six;
        font-size: size(19);
        font-family: 'appleM';
        font-weight: 500;
        text-align: center;
      }
      .close {
        position: absolute;
        top: size(12);
        right: size(20);
        opacity: 1;
      }
    }
    .modal-body {
      padding: 0 size(20);
    }
    .modal-footer {
      padding: 0 size(20) size(20);
      border: 0;
      button[data-dismiss="modal"] {
        margin-left: 0;
        margin-right: 0;
        font-size: size(14);
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
      }
    }
  }
  // 대화창
  &-center {
    .modal-header {
      position: relative;
      .modal-title {
        width: 100%;
        padding-bottom: 0;
        padding-top: size(6);
        font-size: size(16);
        font-family: 'appleM';
        font-weight: 500;
        text-align: center;
      }
      .close {
        position: absolute;
        top: size(16);
        right: size(20);
        opacity: 1;
      }
    }
    .modal-body {
      font-size: size(13);
      color: $greyish-brown-three;
      text-align: center;
      .agree-detail {
        display: inline-block;
        font-family: 'appleB';
        font-size: size(13);
      }
    }
    .modal-footer {
      padding: 0;
      .btn {
        width: 100%;
        margin: 0;
        font-size: size(15);
      }
    }
  }
}