// -----------------------------------------------------------------------------
// Typography
// -----------------------------------------------------------------------------

// basic style for copy text
//
// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/base/_typography.scss

.heading {
  position: relative;
  font-family: 'appleM';
  font-size: size(22);
  font-weight: 300;
  letter-spacing: size(-1);
  color: $black-two;
  &-sub {
    font-family: 'appleL';
    font-size: size(12);
    color: $warm-grey-three;
    line-height: 1.3;
  }
  // 이용후기 등..
  &-2 {
    font-size: size(17);
  }
  &-3 {
    font-size: size(16);
  }
  &-4 {
    font-size: size(14);
  }

  &-5 {
    font-size: size(12);
  }
  // @include media-breakpoint-up(md) {
  //   font-size: size(28);
  //   &-sub {
  //     font-size: size(14);
  //   }
  // }
  &-bold {
    font-family: 'appleB';
    font-size: size(17);
  }
  &-sb {
    font-family: 'appleSB';
    font-weight: 500;
    font-size: size(16);
  }
  &-close {
    font-family: 'appleM';
    font-weight: 500;
    font-size: size(19);
  }
  &-delete {
    position: relative;
    font-family: 'appleSB';
    font-weight: 500;
    font-size: size(16);
    .btn-close {
      position: absolute;
      top: -10px;
      right: -15px;

    }
  }
}

// more text colors
.text {
  &-periwinkle {
    color: $periwinkle;
  }
}

.txt {
  &-guide {
    font-family: 'appleL';
    font-size: size(13);
  }
  &-eb {
    font-family: 'appleH';
  }
}
