.fixed-tab {
  position: fixed;
  top: size(44);
  z-index: 6;
  background-color: $white;
}
.tab-full {
  background-color: $white;
  .tab-list {
    display: flex;
    justify-content: center;
    // margin: size(12) 0;
    padding: size(10) 0 0;
    border-bottom: 2px solid $pinkish-grey-six;
    li {
      width: 50%;
      a {
        height: size(46);
        font-size: size(16);
        line-height: 50px;
        color: $pinkish-grey-six;
        &.active,
        &:active,
        &:focus {
          color: $primary;
        }
      }
    }
    &::after {
      bottom: -2px;
      height: 4px;
      border-color: $primary;
    }
  }
}