// 메인 이벤트 배너
.banner {
  display: block;
  img {
    width: 100%;
  }
  &-bg_beige {
    background-color: $beige;
  }
  &-bg_iceblue {
    background-color: $ice-blue-three;
  }
  &-bg_red {
    color: $white;
    background-color: $pastel-red;
  }
  &-bg_dark {
    color: $white;
    background-color: $charcoal-grey-two;
  }
  &-bg_violet {
    color: $white;
    background-color: $periwinkle;
  }
  &-enjoy {
    background-color: $ice-blue-four;
  }
  &-kakao {
    background-color: $off-white;
  }
  &-naver {
    background-color: $ice-blue-four;
  }
  &-fb {
    background-color: $off-white-two;
  }
  &.coupon {
    font-family: 'appleL';
    margin-bottom: size(15);
    background-size: contain;
    background-position: right bottom;
    background-repeat: no-repeat;
    .coupon-body {
      padding: size(15) 0;
    }
    .coupon-title {
      font-size: size(15);
      font-family: 'appleL';
      font-weight: 600;
    }
    p {
      margin-bottom: 0;
      font-size: size(13);
      font-weight: 600;
    }
    .coupon-date {
      font-family: 'appleL';
    }
  }
  &-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 55px;
    padding: size(15) size(20);
    font-size: size(14);
    color: $black-three;
    background-color: $white-six;
  }
}