.mypage {
  &-head {
    .my-email {
      font-size: size(14);
    }
  }
}

.point-info {
  display: flex;
  align-items: center;
  padding: size(20) 0;
  background-color: $white-two;
  li {
    width: 33%;
    text-align: center;
    border-left: 1px solid $pinkish-grey-five;
    &:first-child {
      border-left: 0;
    }
    b {
      font-size: size(21);
      font-family: 'appleB';
      letter-spacing: -1px;
      + p {
        margin-bottom: 0;
        font-size: size(13);
        font-family: 'appleM';
      }
    }
  }
}

.list-group {
  &.status {
    .list-group-item {
      padding: 0;
      &:last-child {
        border-bottom-width: 1px;
      }
    }
    a {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1rem 0 .714rem 0;
      p {
        margin: 0;
        font-size: size(15);
        font-family: 'appleL';
        color: $black-three;
        .icon {
          margin-right: 6px;
        }
      }
      .num {
        font-size: size(16);
      }
      .icon-arrow {
        margin-right: 6px;
      }
    }
  }
}