//할인쿠폰 사용 popup
.coupon-count{
    color:$french-blue;
    border:1px solid $pinkish-grey-nine;
    padding:10px 25px;
}
.coupon-popup-btn{
    border:none;
    background:$white-six;
    box-shadow: none;
    width: 40px;
    height: 46px;
}

//coupon list popup

.coupon-modal-dialog{
    .close{
        opacity: 1;
        position: absolute;
        top:15px;
        right:10px;
        padding:0;
    }   

    .modal-content{
        width:21rem;
        margin:0 auto;
        padding:1.875rem 1.375rem 2.188rem 1.375rem;
    }

    .modal-header{
        padding:0;
        position: relative;
        justify-content:center;
    }

    .modal-title{
        font-size: 1.25rem;
        font-family: 'appleM';
        font-weight: 500 !important;
    }

    .modal-body{
        padding:0;
    } 
}

.discount-coupon{
    margin-top: size(13);
    .coupon-group{
        .coupon-list{
            margin-top: size(13) !important;
        }
    }    
}

.coupon-group{
    .coupon-list{
        border: 1px solid $pinkish-grey-five;
        padding: size(17) size(15) size(17) size(15);
        margin-top: size(10);      
        &:first-of-type{
            margin-top: 0;
        }

        label{
            padding-left: size(10);
        }

        .condition{
            font-size: size(13);
            font-family: 'appleL';
        }

        .term{
            font-size: size(12);
            color:$pinkish-grey-eight;
            font-family: 'appleL';
        }

        &.selected{
            border: 1px solid $french-blue;
        }

    }
}


//input button between
.form-between{
  display: flex;
  margin-top: size(14);
  .fix-input{
    width: calc(100% - 7rem);
  }
  .fix-btn{
    width:7rem;
    margin-left: 8px;
  }

  .fix-btn-outline{
    border:1px solid #90a2f9;
    color:$french-blue;
    width:7rem;
    margin-left: 8px;
  }
}

//예약정보 체크인 체크아웃
.checkinout-area{
    margin-top: size(22);
    label{
        font-family: 'appleL';
    }
    .checkout{
        margin-left: size(16);
    }
    .date{
        width:size(90);
        padding:0.563rem;
    }

    .time{
        width:size(42);
        margin-left: size(5);
        padding:0.563rem;
    }
}

.select-time-section{
    margin-top: size(17);

    .time-group{
        margin-top: size(14);

        .list{
            border:1px solid $pinkish-grey-nine;
        }
    }
}