// -----------------------------------------------------------------------------
// Footer of the site/application
// -----------------------------------------------------------------------------
.footer {
  height: 66px;
  border-top: 1px solid $pinkish-grey;
  color: $warm-grey;
  background-color: $white;
  .nav {
    display: flex;
    justify-content: space-between;
    // align-items: center;
    li {
      text-align: center;
    }
  }
  a {
    display: block;
    width: 44px;
    font-size: size(10);
    text-align: center;
    color: $warm-grey;
    // letter-spacing: 0;
    &.active {
      font-family: 'appleEB';
      font-size: size(10);
      color: $primary;
      .icon-nav {
        background-color: $primary;
      }
    }
    .icon-nav {
      + span {
        display: block;
        margin-top: 4px;
      }
    }
  }
  // 공지 고객센터
  &-cs {
    background-color: $white-three;
    .list-menu {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 60px;
      li {
        display: inline-block;
        margin-right: 1rem;
        a {
          font-family: 'appleM';
          font-size: size(14);
          color: $black-two;
        }
      }
    }
    .btn {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding: 0;
      width: 100%;
      .icon-arrowbold {
        width: 12px;
        height: 12px;
        background-size: contain;
        background-position: center;
      }
    }
  }
  &-qa {
    background-color: $white-six;
    .content {
      font-size: size(14);
      font-family: 'appleL';
    }
    .on-time {
      margin-bottom: size(21);
      font-family: 'appleSB';
      font-size: size(13);
      color: $black-two;
      p {
        margin: 3px 0;
      }
      blockquote {
        font-family: 'appleL';
        font-size: size(13);
        color: $warm-grey-three;
      }
    }
    .move-top {
      width: size(92);
      padding-left: 14px;
      padding-right: 14px;
      font-size: size(13);
      font-family: 'appleL';
      > * {
        vertical-align: middle;
      }
    }
  }
  &-copy {
    margin: 2.5rem auto;
    p {
      margin-bottom: 0;
      font-size: size(13);
    }
  }
  // 고정 버튼
  &-button {
    height: size(80);
    border: 0;
    @at-root main {
      padding-bottom: size(80);
    }
  }
}
